<template>
  <div>
    <a-modal 
      v-model="visible" 
      :confirmLoading="loading" 
      :maskClosable="false" 
      @cancel="cancel" 
      @ok="confirm" 
      :width="800"
    >
      <div slot="title">店铺信息</div>
      <div class="modal-body">
        <h3>基本信息</h3>

        <div class="row" v-if="type=='modify'">
          <div class="columns">
            <span class="label" style="width: 71px;">店铺ID:</span>
            <span class="value" style="width: 630px;"> {{ form.id }} </span>
          </div>
        </div>

        <div class="row">
          <div class="columns">
            <span class="label" style="width: 71px;">店铺名称:</span>
            <a-input v-model="form.name" style="width: 630px;" />
          </div>
        </div>

        <div class="row">
          <div class="columns">
            <span class="label" style="width: 71px;">api_id:</span>
            <a-input v-model="form.api_id" style="width: 630px;" />
          </div>
        </div>

        <div class="row">
          <div class="columns">
            <span class="label" style="width: 71px;">api_key:</span>
            <a-input v-model="form.api_key" style="width: 630px;" />
          </div>
        </div>

        <div class="row" v-if="type=='modify'">
          <div class="columns">
            <span class="label" style="width: 71px;" >创建时间:</span>
            <span class="value">{{ form.create_time }}</span>
          </div>
        </div>

        <div class="row" v-if="type=='modify'">
          <div class="columns">
            <span class="label" style="width: 71px;" >修改时间:</span>
            <span class="value" style="width: 630px;">{{ form.modify_time }}</span>
          </div>
        </div>

        <div class="row" v-if="type=='modify'">
          <div class="columns">
            <span class="label" style="width: 71px;" >负责人员:</span>
            <span class="value" style="width: 630px;">{{ form.owner?form.owner.name:"暂无" }}</span>
          </div>
        </div>
    </div>
    </a-modal>
  </div>
</template>

<script>
import {addShopData,modifyShopData } from '@/api/system'
import { message } from "ant-design-vue";

export default {
  name: 'FormModal',
  props: ['visible', 'form', 'type'],
  model: { prop: 'visible', event: 'cancel' },
  data() {
    return {
      loading: false,
    };
  },
  methods: {
    confirm() {
      if(this.type == "add"){    
        addShopData(this.form).then(
          res => {
            message.success(res.msg)
            this.$emit('update', false);
          }
        )

      }else if(this.type == "modify"){
        modifyShopData(this.form).then(
          res => {
            message.success(res.msg)
            this.$emit('update', false);
          }
        )
      }
    },
    cancel() {
      this.$emit('cancel', false);
    },
  },
}
</script>

<style scoped>
.modal-body { 
  max-height: 60vh;
  overflow-y: auto; 
}

.row {
  display: flex;
  align-items: center;
  margin-top: 5px;
}

.row .columns {
  display: flex;
  margin-right: 30px;
  align-items: center
}

.row .columns .label{
  width: 100px;
  margin-right: 5px;
}

.row .columns .value{
  margin-right: 5px;
  color: green;
}
</style>